import React = require("react");
import Column = require("./Column");

interface Props{
    data?:Column[];
    children?:any[];
}
interface State{

}

class Grid extends React.Component<Props,State>{

    static propTypes:Props={

    }

    constructor(props:Props) {
        super(props);
    }

    render(){

        let props = this.props;
        let data = props.data || [];
        let columns = props.children;
        console.log(columns[0].props.children);

        return <table>
            <tbody>
            {data.map((d,rowIndex)=>{
                console.log('rowIndex',rowIndex);
                let columnsContents = [];
                columns.map((c,colIndex)=>{
                    columnsContents.push(c.props.children(c,rowIndex));
                });
                return <tr key={rowIndex}>{columnsContents}</tr>
            })}
            </tbody>
        </table>;
    }

}

export = Grid;



